// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import VueMaterial from 'vue-material'    /*   Material */
import 'vue-material/dist/vue-material.min.css'
import 'vue-material/dist/theme/default.css'
import "vue-material-design-icons/styles.css"
import App from './App'
import router from './router'
import axios from 'axios'
import qs from 'qs'       //jsondata
import Vuex from 'vuex'
import createPersistedState from "vuex-persistedstate"
import root_url from '../src/api/api'
import $ from 'jquery'
import Vueg from'vueg';

// import lottie from 'node_modules/vue-lottie/src/lottie.vue';
import * as animationData from '../src/assets/lottie/1798-check-animation'
// Vue.component('lottie', lottie);
// import 'vueg/css/transition-min.css';

const Voptions= {
  duration:'0.3',//0.3
  firstEntryDisable:false,//truefalse
  firstEntryDuration:'.6',//.6
  forwardAnim:'fadeInRight',//fadeInRight
  backAnim:'fadeInLeft',//fedeInLeft
  sameDepthDisable:false,//urlfalse
  tabs: [],//[]namename,apptabtab[1]tab[0]backAnimtab[1]tab[2]forwardAnim
  tabsDisable:false,//truetabsfalse
};//disable:false//falsetrue


// import store from './store/store'

Vue.prototype.root_url = root_url;

axios.defaults.timeout = 5000;  //
axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8';           //

axios.get('/static/config.json').then((res) => {
  // 
  Vue.prototype.BASE_URL = res.data.BASE_URL;
  // console.log(res);
});

axios.defaults.withCredentials=true;

Vue.use(Vuex);
Vue.use( Vueg, router, Voptions);



const store = new Vuex.Store({
  state: {
    wsObj: null,        //
    myRsvList: [],    //
    LoginId: null,      //Id
    userNickName: '',       //
    userJobNum: '',         //

    isLogin: false,     //
    RoomMessageContainer: { },
    currentItem: { },     //
    userToken: null,      //token
    isHereOder: false,    //
    isNewOrder: 0,      //
    newPosts: 0,     //
    AppMsg: 0,        //
    OperateMsg: 0,    //
    remindMsg: 0,       //
    isVerify: false
  },
  mutations: {
    changeLoginState (state) {
      state.isLogin = true
    },
    exitLogin (state) {
      state.isLogin = false;
    },
    getCorporationdata(state, params) {
      state.RoomMessageContainer = params;
    },
    setCurrentBrowse(state, params = null) {
      state.currentItem = params;
    },
    setUsertoken(state, params) {
      state.userToken = params;
    },
    setnewPosts(state) {
      state.newPosts = state.AppMsg + state.OperateMsg + state.remindMsg;
    },
    setAppMsg(state, count) {
      state.AppMsg = count;
    },
    setOperateMsg(state, count) {
      state.OperateMsg += count;
    },
    setremindMsg(state, count) {
      state.remindMsg += count;
    },

    setLoginId(state, id) {
      state.LoginId = id;
    },
    setmyRsvList(state, param) {
      state.myRsvList = param;
    },

    updatemyRsvList(state, param) {
      state.myRsvList.push(param);
    },

    setwsObj(state, param) {
      state.wsObj = param;
    },

    clearOperateMsg(state) {
      state.OperateMsg = 0;
    },
    setisHereOder(state) {
      state.isHereOder = true;
    },
    setVerify(state) {
      state.isVerify = true;
    },
      setisNewOrder(state) {
      state.isNewOrder ++ ;
    }

  },
  plugins: [createPersistedState()]
});



Vue.prototype.$http=axios;    //:this.$axios
/*axios.defaults.baseURL = 'http://test.icms.work/api/v1';
axios.defaults.headers.common['Authorization'] = AUTH_TOKEN;
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded';*/
Vue.prototype.qs = qs;           //:this.qs
Vue.use(VueMaterial);
Vue.config.productionTip = false;

/* eslint-disable no-new */
new Vue({
  el: '#app',
  store,
  router,
  components: { App },
  template: '<App/>',
  data: {
    eventHub: new Vue()
  }
});



